import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../../../auth/_services/user.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'app-vendor-password',
    templateUrl: "vendor-password.component.html",
    styles: []
})
export class VendorPasswordComponent implements OnInit {


    private formSumitAttempt: boolean;
    public vendorDetails: any = {};
    public isReadOnly = true;
    public ImgUrl: any;
    form: FormGroup;
    pform: FormGroup;
    user_name = '';
    public user_id: any;


    constructor(private imgBaseUrl: ScriptLoaderService, private router: Router, private formBuilder: FormBuilder, private toastr: ToastrService, private route: ActivatedRoute, private _dataService: DataService, private _userService: UserService) {
        this.form = formBuilder.group({ user_name: [null, Validators.required], });
        this.pform = formBuilder.group({ password: [null, Validators.required], });
    }



    ngOnInit() {


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.subscription_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/



        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('vendor');
        this.route.params.subscribe(
            params => {
                localStorage.setItem('section', 'vendor');
                this.user_id = params.id;
                this._userService.getById(params.id).subscribe(
                    data => {

                        this.vendorDetails = data.success;
                        this.user_name = data.success.user_name;
                    },
                    error => {
                        this.toastr.error('Sorry! we can not connect service', 'Error Message');
                    }
                );
            }
        );


    }

    onSubmit() {
        let input: any;
        input = { form_data: this.form.value };
        console.log('input');
        console.log(input);
        input.form_data.user_id = this.user_id;
        this._userService.update_username(input).subscribe(
            data => {
                console.log('data');
                console.log(data);
                this.toastr.success(data.success, 'Success Message');
                this.router.navigate(['vendor/vendor-list']);
            },
            error => {
                this.toastr.error('Sorry! we can not connect service', 'Error Message');
            }
        );
    }

    onSubmitPassword() {
        let input: any;
        input = { form_data: this.pform.value };
        input.form_data.user_id = this.user_id;

        this._userService.update_password(input).subscribe(
            data => {
                this.toastr.success(data.success, 'Success Message');
                this.router.navigate(['vendor/vendor-list']);
            },
            error => {
                this.toastr.error('Sorry! we can not connect service', 'Error Message');
            });
    }


}
